<template>
  <a-layout class="components-layout">
    <a-layout-header class="header">
      <div class="logo" />
    </a-layout-header>
    <a-layout>
      <a-layout-sider width="200" class="nav-menu" style="background: #fff">
        <a-menu
          mode="inline"
          :openKeys="openKeys"
          :style="{ height: '100%', borderRight: 0 }"
          @openChange="onOpenChange"
          :defaultSelectedKeys="[selectedKeys]"
        >
          <a-sub-menu v-for="(item_1, index_1) in nav" :key="index_1+''">
            <span slot="title">
              <a-icon :type="item_1.iconType" />
              {{ item_1.title }}
            </span>
            <a-menu-item
              v-for="(item_2) in item_1.childs"
              :key="item_2.url"
              @click="navClick"
            >{{item_2.title}}</a-menu-item>
          </a-sub-menu>
        </a-menu>
      </a-layout-sider>
      <a-layout style="padding: 0 0 0 10px">
        <a-breadcrumb style="margin: 16px 0">
          <a-breadcrumb-item v-for="(v,i) in breadcrumb" :key="i">{{ v }}</a-breadcrumb-item>
          <!-- <a-breadcrumb-item>List</a-breadcrumb-item>
          <a-breadcrumb-item>App</a-breadcrumb-item>-->
        </a-breadcrumb>
        <a-layout-content
          style="height: calc(100vh - 155px); background-color: #ffffff;overflow-y: auto; padding: 10px"
        >
          <!-- <div style="height: 10000px">1234</div> -->
          <router-view></router-view>
        </a-layout-content>
      </a-layout>
    </a-layout>
  </a-layout>
</template>
<script>
import nav from './nav.json'

// 初始化展开的key
const getInitOpenKey = () => {
  for (let i = 0; i < nav.length; i++) {
    const { childs } = nav[i]
    if (Array.isArray(childs)) {
      for (const item of childs) {
        if (item.url === location.pathname) {
          return i
        }
      }
    }
  }
}

// 获取面包屑菜单
const getBreadcrumb = url => {
  for (let i = 0; i < nav.length; i++) {
    const { childs } = nav[i]
    if (Array.isArray(childs)) {
      for (const item of childs) {
        if (item.url === url) {
          return [nav[i].title, item.title]
        }
      }
    }
  }
}

export default {
  data() {
    return {
      collapsed: false,
      openKeys: [getInitOpenKey() + ''],
      rootSubmenuKeys: nav.map((_, index) => index + ''),
      nav: nav,
      selectedKeys: location.pathname, // 选中的二级菜单
      breadcrumb: getBreadcrumb(location.pathname) // 显示的面包屑
    }
  },
  methods: {
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        key => this.openKeys.indexOf(key) === -1
      )
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : []
      }
    },
    navClick(e) {
      // console.log(e)
      // console.log(location.pathname)
      if (location.pathname != e.key) {
        this.breadcrumb = getBreadcrumb(e.key)
        this.$router.push({ path: e.key })
      }
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less">
.components-layout {
  height: 100%;
  .header {
    background-color: #435c71;
  }
  .logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 28px 16px 0;
    float: left;
  }
  .nav-menu {
    height: calc(100vh - 70px);
    overflow-y: auto;
    overflow-x: hidden;
  }
  .main-window {
    height: calc(100vh - 1000px);
    background-color: #ffffff;
    overflow-y: auto;
  }
}
</style>
